<template>
    <div class="root-div">
        <div class="content">
            <img :src="image.mt" class="img">
            <div class="boxs">
                <div class="box">
                    <div class="hongbao">
                        <div class="hongbao-left">
                            <div class="hongbao-left-son">
                                <div class="hongbao-left-text">
                                    <div class="hongbao-left-text-son"><font class="hongbao-left-text2" v-if="!showMoney"><b>?</b></font><font class="hongbao-left-text1" v-if="showMoney">￥</font><font class="hongbao-left-text2" v-if="showMoney">0</font><font class="hongbao-left-text1" v-if="showMoney">.{{moneyNum}}</font></div>
                                </div>
                            </div>
                        </div>
                        <div class="hongbao-right">
                            <div class="hongbao-right-son1">
                                <font class="hongbao-right-text1">通用红包</font>
                                <br>
                                <font class="hongbao-right-text2">消费满35元 即可使用</font>
                            </div>
                            <div class="hongbao-right-son2">
                                赚现金
                            </div>
                        </div>
                    </div>
                    <div class="desc">
                        登录 美团APP 即可使用
                    </div>
                    <div @click="randomMoney" class="btn" :data-clipboard-text="code">
                        <template v-if="!showMoney">领取红包</template>
                        <template v-else>已领取</template>
                    </div>
                </div>
                <div class="box">
                    <div class="box-title">
                        <del>&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;</del>
                        <div>活动规则</div>
                        <del>&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;</del>
                    </div>
                    <div class="box-text-ui">1. 下单即可分享给小伙伴</div>
                    <div class="box-text-ui">2. 每个链接仅限20人领取</div>
                    <div class="box-text-ui">3. 每个链接会随机发放三个大额红包</div>
                    <div class="box-text-ui">4. 每人每天仅限领取1次</div>
                    <div class="box-text-ui">5. 红包仅限在线支付使用，且收餐人手机号、领取红包时输入的手机号和账号中的手机号需为同一手机号</div>
                    <div class="box-text-ui">6. 其他未尽事宜，请咨询客服</div>
                </div>
            </div>



        </div>
        <transition name="fade">
            <div class="toast" v-if="showToast">你已经抢过这个红包了</div>
        </transition>

    </div>

</template>

<script>
    import Cookies from 'js-cookie'
    import Clipboard from 'clipboard'
    export default {
        data() {
            return {
                image: {
                    mt: require("../assets/mt.png")
                },
                code: "",
                showMoney: false,
                moneyNum: "4",
                showToast: false,
            }
        },
        mounted() {

            let Base64 = require('js-base64').Base64
            this.code = Base64.decode('WHVwYzY2MDNlRQ==')
            let clipboard = new Clipboard('.btn')
            let nowDate = new Date().toLocaleString().split(" ")[0]
            let cookieDate = Cookies.get("DATE2")
            if (nowDate == cookieDate) {
                this.moneyNum = Cookies.get("MONEY2")
                this.showMoney = true
                this.showToast = true
                setTimeout(() => this.showToast = false, 2000)
            } else {
                this.moneyNum = (new Date().getTime()) % 3 + 4 + ""
                this.showMoney = false
            }
        },
        methods: {
            randomMoney() {
                if (!this.showMoney) {
                    this.showMoney = true
                    Cookies.set("DATE2", new Date().toLocaleString().split(" ")[0], { expires: 1 })
                    Cookies.set("MONEY2", this.moneyNum, { expires: 1 })
                }
            }
        }
    }
</script>

<style>
    .root-div {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .content {
        display: flex;
        flex-direction: column;
        align-items: stretch;
        width: 100%;
        background: #893031;
        min-height: 100vh;
    }

    .img {
        width: 100%;
    }

    .boxs {
        display: flex;
        flex-direction: column;
        align-items: stretch;
        width: 100%;
        padding: 0px 3vw 15vw 3vw;
        box-sizing: border-box
    }

    .box {
        margin-top: 5vw;
        display: flex;
        border-radius: 4px;
        flex-direction: column;
        align-items: center;
        width: 100%;
        background: #d0abac;
        padding: 3vw;
        box-sizing: border-box;
    }

    .hongbao {
        display: flex;
        width: 100%;
        justify-content: center;
        align-items: center;
        height: 20vw;
        box-sizing: border-box;
    }

    .hongbao-left {
        z-index: 2;
        border-radius: 3px 0px 0px 3px;
        background: #fff;
        width: 26%;
        height: 94%;
        padding: 1vw 0px 1vw 1vw;
        box-sizing: border-box;
    }

    .hongbao-left-son {
        border-radius: 3px 0px 0px 3px;
        border-bottom: 1px solid #e8d6bd;
        border-top: 1px solid #e8d6bd;
        border-left: 1px solid #e8d6bd;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        box-sizing: border-box;
    }

    .hongbao-left-text {
        width: 100%;
        height: 100%;
        color: #ea3248;
        display: flex;
        justify-content: center;
        align-items: center;
        box-sizing: border-box;
        /* background: #606266; */
    }

    .hongbao-left-text-son{
        display: flex;
        justify-content: center;
        align-items: baseline;
    }

    .hongbao-left-text1 {
        font-size: 7vw;
    }

    .hongbao-left-text2 {
        font-size: 11vw;
    }

    .hongbao-right {
        border-radius: 3px 3px 3px 3px;
        background: #f54229;
        width: 74%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        box-shadow: -0.5vw 0px 1px #a8190971;
    }

    .hongbao-right-son1 {
        width: 50%;
        height: 65%;
        color: #fff;
    }

    .hongbao-right-text1 {
        font-size: 4.5vw;
        font-weight: 600;
        line-height: 7.2vw;
    }

    .hongbao-right-text2 {
        color: #fddeca;
        font-size: 2.8vw;
    }

    .hongbao-right-son2 {
        margin-left: 6vw;
        width: 16vw;
        height: 8vw;
        background: #fcde87;
        border-radius: 4vw;
        color: #7d490e;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 3.5vw;
    }

    .desc {
        margin-top: 3vw;
        color: #341d00;
        font-size: 3vw;
    }

    .btn {
        margin-top: 3vw;
        border-radius: 4px;
        width: 100%;
        background: #ff3f26;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #fff;
        font-size: 5.5vw;
        line-height: 12vw;
    }

    .box-title {
        width: 100%;
        display: flex;
        justify-content: space-between;
        color: #b99794;
        font-size: 4vw;
        line-height: 9vw;
        margin-bottom: 2vw;

    }

    .box-title div {
        font-size: 4.6vw;
        color: #333333;
    }

    .box-text-ui {
        color: #444444;
        width: 97%;
        font-size: 4vw;
        margin-bottom: 2vw;
    }

    .toast {
        z-index: 10;
        position: fixed;
        top: 45vh;
        width: 54vw;
        left: 23vw;
        background: #222121c2;
        color: #ffffffe3;
        border-radius: 4px;
        font-size: 4.2vw;
        line-height: 8.4vw;
        text-align: center;

    }

    .fade-enter-active,
    .fade-leave-active {
        transition: opacity 0.8s;
    }

    .fade-enter,
    .fade-leave-to {
        opacity: 0;
    }
</style>


